raul-avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  width: 2.5rem;
  height: 2.5rem;
  background-color: #EBEDEE;
  font-size: 0.875rem;
}

raul-avatar .raul-avatar__icon {
  color: #9BA3A7;
  font-size: 1.25rem;
}

raul-avatar .raul-avatar__image {
  width: 2.5rem;
  height: 2.5rem;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}

raul-avatar.r-rounded-full .raul-avatar__image {
  border-radius: 9999px;
}

raul-avatar[primary] {
  background-color: #0076CC;
}

raul-avatar[primary] .raul-avatar__icon {
  color: #fff;
}

raul-avatar[small] {
  width: 2rem;
  height: 2rem;
}

raul-avatar[small] .raul-avatar__icon {
  font-size: 1rem;
}

raul-avatar[small] .raul-avatar__image {
  width: 2rem;
  height: 2rem;
}